body {
  font-family: "Nunito", sans-serif;
  font-style: normal;
  font-weight: 400;
}





/* mobile nav starts */


.productImg {
  transition: all 1s !important;
  transform: scale(1);
  filter: grayscale(100%)

}

.productImg:hover {
  transform: scale(0.9);
  filter: grayscale(0%);
}

.animtitle {
  transition: all 1s ease-in-out !important;
  color:white;
  

}

.animtitle:hover {
  color: #231f20 !important;
  letter-spacing: 3px !important;
  text-shadow: 0px 0px 4px rgb(255, 255, 255) !important;
background-color: rgba(235, 235, 235, 0.726);
}




@keyframes greying {
  from {filter: grayscale(0%);}
  to {filter: grayscale(100%);}
}

