/* base elements */
/* ------------------------------------- */

* {
  box-sizing: border-box;
}
body {
  /*background:url("../images/bg.gif") repeat-x scroll 0 0 #FFF;*/
  color: #333333;
  /*font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;*/
  font-family: "BrandonGrotesqueWeb-Regular";
  font-size: 12px;
  line-height: 1.5;
  text-transform: none;
}
.largeBannerTxt {
  font-size: 26px;
  letter-spacing: 3px;
}
.desktop-only {
  display: block;
}
.mobile-only {
  display: none;
}
.mobile-cart {
  display: none;
}
.desktop-cart {
  display: table-row;
}
a {
  color: #515151;
  outline: none;
  overflow: hidden;
  text-decoration: none;
}
a:hover {
  color: currentColor;
}
button:focus {
  outline: none;
  box-shadow: none;
}
fieldset {
  border: 0 none;
  margin: 0;
  padding: 0;
}
table {
  width: 100%;
}
th {
  text-align: left;
}
td {
  padding: 1em;
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5 {
  font-size: inherit;
  margin: 0;
  padding: 0;
  font-weight: normal;
}

.homeBanner {
  font-family: "Nunito", sans-serif;
  text-align: center;
}

/* helper classes */
/* ------------------------------------- */
.fr {
  float: right;
}
.fl {
  float: left;
}
.clearfix:after {
  clear: both;
  content: " ";
  display: block;
  font-size: 0;
  height: 0;
  visibility: hidden;
}
.visually-hidden {
  border: 0 none;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  display: none;
}
.ie7 .visually-hidden {
  width: 1px !important;
}
ul.menu {
  float: left;
  margin: 0;
  padding: 0;
}
ul.menu li {
  float: left;
  list-style: none outside none !important;
}

.toggle-trigger {
  cursor: pointer;
  position: relative;
}

a:hover,
a:active,
a:focus {
  outline: 0;
}

/* page layouts */
/* ------------------------------------- */

#wrapper {
  background: #fff;
  margin: 0 auto;
  max-width: 100%;
  font-size: 12px;
}

header {
  position: relative;
}

#primary {
  float: left;
  width: 100%;
}

.pt_product-search-result #primary {
  float: left;
  width: 75%;
}

#secondary {
  float: left;
  width: 25%;
}

#main {
  background-color: #fff;
}

.full-width #primary {
  border: 0 none;
  width: 100%;
}

/* jQuery Ui */
/* ------------------------------------- */

.ui-widget-overlay {
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACgAAABkBAAAAAAU/8CnAAAAHklEQVR42mMwxgIYRgVHBUcFRwVHBUcFRwVHBVEBAJ9SjoCoVzXBAAAAAElFTkSuQmCC) repeat scroll 50%
    50% #333333;
  opacity: 0.4;
}
.ui-dialog {
  background: none repeat scroll 0 0 #ffffff;
}
/*
.ui-dialog .ui-widget-header{background:none repeat scroll 0 0 #F3F3F3;border:0 none;border-radius:0 0 0 0;height:auto; min-height: 32px;text-transform:uppercase;}
*/
.ui-dialog .ui-dialog-title {
  font-weight: bold;
}

.ui-dialog .ui-icon-closethick {
  text-indent: 0;
  color: transparent !important;
}
.ui-dialog .ui-icon-closethick:after {
  content: "\f00d";
  font-family: "fontawesome";
  color: #000;
  font-size: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.ui-dialog .ui-dialog-titlebar-close {
  border: 0 none;
  border-radius: 0 0 0 0;
  height: 32px;
  margin: 0;
  padding: 0;
  right: 0;
  top: 0;
  width: 32px;
}
.ui-dialog .ui-dialog-titlebar-close span {
  margin: 0;
  padding: 0;
}
.ui-dialog h1,
.ui-dialog h2 {
  font-family: "Nunito";
  text-transform: uppercase;
  color: #333333;
  font-size: 20px;
  letter-spacing: 5.5px;
  text-align: center;
}
.ui-dialog .dialog-required {
  font-weight: 400;
  position: relative;
  text-transform: none;
}
.ui-accordion .ui-accordion-content {
  padding: 0;
}
.ui-tabs .ui-tabs-nav {
  background: none repeat scroll 0 0 transparent;
  border: 0 none;
  height: 2.45em;
  padding: 0;
}
.ui-tabs .ui-tabs-nav li {
  -moz-border-bottom-colors: none;
  -moz-border-image: none;
  -moz-border-left-colors: none;
  -moz-border-right-colors: none;
  -moz-border-top-colors: none;
  background: none repeat scroll 0 0 #efefef;
  border-color: #d9d9d9 #d9d9d9 #ffffff;
  border-style: solid;
  border-width: 1px;
  font-family: "Bodoni MT W01";
  font-style: italic;
  list-style: none outside none !important;
  margin: 0 1em -1px 0;
}
.ui-tabs .ui-tabs-nav li.ui-state-active {
  background: none repeat scroll 0 0 #ffffff;
}
.ui-tabs .ui-tabs-nav li.ui-state-active a {
  color: #666666;
}
.ui-tabs .ui-tabs-nav li a {
  color: #999999;
}
.ui-tabs .ui-tabs-panel {
  background: none repeat scroll 0 0 #ffffff;
  border-color: #d9d9d9;
  border-radius: 0 0 0 0;
  border-style: solid;
  border-width: 1px;
  box-shadow: 4px 2px 10px 0 rgba(66, 66, 66, 0.1);
  color: #414141;
  font-size: 0.9em;
  line-height: 1.5em;
  margin: 0;
  padding: 20px 10px;
}

/* form components */
/* ------------------------------------- */

button[disabled],
input[disabled] {
  color: #cccccc;
}

button {
  background: #000;
  color: #fff;
  font-family: "Nunito";
  padding: 0 15px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: none;
  height: 36px;
  min-width: 120px;
}
.button-fancy-small {
  background: #000;
  color: #ffffff !important;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
}
.button-fancy-medium {
  background: #000;
  color: #ffffff !important;
  font-weight: 400;
  text-transform: uppercase;
  border: none;
}
.button-fancy-large {
  background: #000;
