/* figure class elements - these are used on many of the pages below */
/* ------------------------------------- */

.figure {
  position: relative;
  display: inline-block;
  height: auto;
  padding: 0;
  margin: 0;
  color: white;
}
.figure img {
  height: auto;
  padding: 0;
  margin: 0;
  width: 100%;
}
.figure:hover .figcaption-hover {
