}
.figcaption-title {
  position: absolute;
  bottom: -22px;
  left: 0;
  font-size: 1.5em;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  background-color: #fff;
  width: 100%;
  text-align: center;
  font-weight: bold;
  color: #000;
}
.figcaption-hover {
  opacity: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  font-size: 2em;
  transition: opacity 0.5s ease-out;
}
.figcaption-hover .title {
  display: block;
  color: white;
  padding-top: 175px;
  font-size: 0.6em;
  font-weight: bold;
  letter-spacing: 0.25em;
  text-transform: uppercase;
}
.figcaption-hover .body {
  display: block;
  color: white;
  padding: 10px 10px 10px 10px;
  font-size: 1.3em;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  line-height: 1em;
}

