.second-heading{
    font-family: BaskervilleBT-Roman;
    font-size:20px;
    color: #1C1C1C;
    margin:0;
    letter-spacing: 3.2px;
    line-height: 49px;
    text-transform: uppercase;
}
.section{
    margin-bottom:2%;
}
.sub-section-heading{
    font-family: BaskervilleBT-Roman;
    font-size:40px;
    font-weight:500;
    color: #1C1C1C;
    margin:0;
    line-height: 49px;
    margin-bottom:1.5%
}
.sub-section-headingLarge{
    font-family: BaskervilleBT-Roman;
    font-size:45px;
    font-weight:500;
    color: #1C1C1C;
    margin:0;
    line-height: 49px;
    margin-bottom:1.5%
}
.sub-section-content{
    font-family: BaskervilleBT-Roman;
    font-size:22px;
    color: #1C1C1C;
    margin:0;
    line-height: 35px;
}
.sub-section-list{
    list-style-type:none;
    line-height: 35px;
    text-align: center;
    font-family: BaskervilleBT-Roman;
    font-size:22px;
    color: #1C1C1C;
    margin:0;
    padding-left: 0;
}
.menu-table{
    width:60%;
    padding:1.5%;
    margin-left:20%;
    font-family: FuturaPT-Book;			
}
.main-image{
    width:100%;
}
@media screen and (max-width: 720px) {
    .menu-table{
        visibility: hidden;
        clear: both;
        display: none;
    }
    .main-image{
        width:100%;
    }
}
.contact-us-button{
    background: #fff;
    color:#222222 !important;
    padding: 2% 4%;
text-decoration: none;
text-transform: uppercase;
    font-size:12px;
    font-family: FuturaPT-Book;
    letter-spacing: 2.8px;
    border: 1px solid #4A4A4A;
}
.content-asset {
    margin-top: -25px;
}
.category-refinement {
    padding: 0px !important;
}
.category-refinement.fixed-top {
    padding-bottom: 0px !important;
    top: -20px !important;
}
.container {
    width: 100%;
}
